#CardHolderScreen {

    ons-row.add-card-holder {
		padding: 8px 8px;
		ons-button {
			width: 100%;
			text-align: center;
			border-radius: 0px;
			border: 1px dashed;
			height: 60px;
			line-height: 60px;
			background: rgba(255, 255, 255, 0.5);
        }
    }
    
	ons-modal#card-holder {
        ons-row.total-row {
            font-size: 25px;
            border-top: 1px solid #ccc;
            padding-top: 12px;
        }
        ons-row.total-row-top-bottom {
            font-size: 15px;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            padding-top: 10px;
            padding-bottom: 10px;
        }
    }

    ons-modal.card-holder-modal {
        padding: 1rem;
        width: 100%;

        div.per-hour-holder {
            position: relative;
            text-align: right;

            div.currency {
                display: inline-block;
                position: absolute;
                font-size: 22px;
                font-weight: bold;
            }

            div.rate {
                font-weight: bold;
                display: inline-block;
                margin-left: 12px;
            }

            div.per-hour {
                font-weight: bold;
                font-size: 15px;
            }
        }

        div.modal-container {
            padding: 0.6rem;
            background: #fff;
            color: #030303;
            padding: 1rem 0.6rem;
            border-radius: 4px;
        }
        div.modal__content {
            margin: 0px auto;
        }
        .custom-select {
            background: none;
        }
        ons-col {
            margin-bottom: 10px;
            text-align: left;
            padding-left: 0.3rem;
            padding-right: 0.3rem;
            label {
                font-size: 12px;
                margin-left: 2px;
                margin-bottom: 5px;
                color: #424242;
            }
        }
        ons-input {
            width: 100%;
        }
    }

    ons-modal#edit-card-holder {
        ons-row.total-row {
            font-size: 25px;
            border-top: 1px solid #ccc;
            padding-top: 12px;
        }
        ons-row.total-row-top-bottom {
            font-size: 15px;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            padding-top: 10px;
            padding-bottom: 10px;
        }
    }
    .button-add {
        background-color: #ffffff;
		border: none;
		color: black;
		font-size: 16px;
		cursor: pointer;
		border-radius: 19px;
		position: fixed;
		top: 50px;
		right: 5px;
		margin-top: -37px;
		height: 9px;
		width: 60px;
		padding-bottom: 23px;
		padding-left: 7px;
		font-weight: bold;
    }
    .heading-info {
        font-weight: bold;
        text-align: center;
        font-size: 16px !important;
    }
    .card-holder-icon{
        float: left;
        height: 1rem;
        margin-top: 12px;
        padding-right: 11px;
    }
    .right-icon{
        display: flex;
        justify-content: flex-end;
        padding-top: 10px;
    }
    .card-header-container{
        margin-top: 4rem;
        position: relative;
        background: #00ccff !important;
        color: white !important;
        height: 9rem !important;
        box-shadow: 2px 1px 5px 0px;
    }


    ons-modal.edit-card-holder-modal {
        padding: 1rem;
        width: 100%;

        div.per-hour-holder {
            position: relative;
            text-align: right;

            div.currency {
                display: inline-block;
                position: absolute;
                font-size: 22px;
                font-weight: bold;
            }

            div.rate {
                font-weight: bold;
                display: inline-block;
                margin-left: 12px;
            }

            div.per-hour {
                font-weight: bold;
                font-size: 15px;
            }
        }

        div.modal-container {
            padding: 0.6rem;
            background: #fff;
            color: #030303;
            padding: 1rem 0.6rem;
            border-radius: 4px;
        }
        div.modal__content {
            margin: 0px auto;
        }
        .custom-select {
            background: none;
        }
        ons-col {
            margin-bottom: 10px;
            text-align: left;
            padding-left: 0.3rem;
            padding-right: 0.3rem;
            label {
                font-size: 12px;
                margin-left: 2px;
                margin-bottom: 5px;
                color: #424242;
            }
        }
        ons-input {
            width: 100%;
        }
    }
    
    
}